import React from 'react';
import Button from '@material-ui/core/Button';
import Menu from '@material-ui/core/Menu';
import MenuItem from '@material-ui/core/MenuItem';
import ArrowDropDownIcon from '@material-ui/icons/ArrowDropDown';
import WebIcon from '@material-ui/icons/Web';
import PhoneIphoneIcon from '@material-ui/icons/PhoneIphone';
import CloudIcon from '@material-ui/icons/Cloud';
import DetailsIcon from '@material-ui/icons/Details';
import SettingsInputComponentIcon from '@material-ui/icons/SettingsInputComponent';
import { makeStyles } from '@material-ui/core';





const useStyles = makeStyles((theme) => ({

    extendedIcon: {
        marginRight: theme.spacing(1),
      },
    
  }));

export default function DropdownMenu(props) {

    const classes = useStyles();
  const [anchorEl, setAnchorEl] = React.useState(null);

  const handleClick = (event) => {
    setAnchorEl(event.currentTarget);
  };

  const handleClose = () => {
    setAnchorEl(null);
  };

  

  const {heading,child1,child2,child3,child4,child5} = props.list;

  return (
    <div>
      <Button color="inherit" aria-controls="simple-menu" aria-haspopup="true" onClick={handleClick}>
       {heading} <ArrowDropDownIcon/>
      </Button>
      <Menu
        style={{
            marginTop: '50px',           
        }}
        id="simple-menu"
        anchorEl={anchorEl}
        keepMounted
        open={Boolean(anchorEl)}
        onClose={handleClose}
      >
          
        <MenuItem onClick={handleClose}><WebIcon className={classes.extendedIcon}/>
        {child1}
        </MenuItem>
        <MenuItem onClick={handleClose}><PhoneIphoneIcon className={classes.extendedIcon}/>{child2}</MenuItem>
        <MenuItem onClick={handleClose}><SettingsInputComponentIcon className={classes.extendedIcon}/>{child3}</MenuItem>
        <MenuItem onClick={handleClose}><CloudIcon className={classes.extendedIcon}/>{child4}</MenuItem>
        <MenuItem onClick={handleClose}><DetailsIcon className={classes.extendedIcon}/>{child5}</MenuItem>
        
       
      </Menu>
    </div>
  );
}